import Actions from "./../../redux/actionTypes"
import axios from "axios"

const url=`http://wanervalencia2019.pythonanywhere.com`

export const register=(first_name,last_name,username,email,password)=>(dispatch)=>{
  const params={
    first_name,
    last_name,
    username,
    email,
    password
  }
  const config={

  }
  axios.post(`${url}/api/v1/auth/register`,params,config)
  .then((res)=>{
    return dispatch({
        type: Actions.LOGIN_SUCCES,
        user: res.data.User,
        token: res.data.Token,
    });
  })
  .catch((err)=>{
    console.log("Verifica tu conexin");
  })

}
