import Actions from "./../../redux/actionTypes";
import axios from "axios";

const url=`http://wanervalencia2019.pythonanywhere.com`

export const getTask = (idUser, token) => async (dispatch) => {
  const config = {
    headers: {
      Authorization: `Token ${token}`,
    },
  };
  axios
    .get(`${url}/api/v1/task/list/?id=${idUser}`, config)
    .then((res) => {
     
      return dispatch({
        type: Actions.GET_TASK_SUCCES,
        tasks: res.data,
      });
    })
    .catch((err) => {
      return dispatch({
        type: Actions.GET_TASK_ERROR,
      });
    });
};
export const getTaskComplete = (token,page=1) => async (dispatch) => {
   dispatch({"type":"OPEN_LOADING"})
  const config = {
    headers: {
      Authorization: `Token ${token}`,
    },
  };
  axios
    .get(`${url}/api/v1/task/list/complete?page=${page}`, config)
    .then((res) => {
      //console.log(res.data);
       dispatch({"type":"CLOSE_LOADING"})
      return dispatch({
        type: Actions.GET_TASK_SUCCES,
        tasks: res.data.result,
        count:res.data.count,
        pagination_pages:res.data.total_pages,
        pagination_links:res.data.links
      });
    })
    .catch((err) => {
      //console.log(err.response)
       dispatch({"type":"CLOSE_LOADING"})
      return dispatch({
        type: Actions.GET_TASK_ERROR,
      });
    });
};
