export const deleteTask = (idTask, token) => async (dispatch) => {
  dispatch({"type":"OPEN_LOADING"})
  const config = {
    headers: {
      Authorization: `Token ${token}`,
    },
  };
  axios
    .delete(`${url}/api/v1/task/delete/${idTask}/`, config)
    .then((res) => {
      dispatch({"type":"CLOSE_LOADING"})
      return dispatch({
        type: Actions.DELETE_SUCESS,
        id: idTask,
      });
    })
    .catch((err) => {
      dispatch({"type":"CLOSE_LOADING"})
      return dispatch({
        type: Actions.DELETE_ERROR,
      });
    });
};

