import React, { useState } from "react";
import PropTypes from "prop-types";
import {CircularProgress,DialogContent,Dialog } from '@material-ui/core';
function Loading(props) {
	const {loading,message}=props
	const dialog = (
		<Dialog open={loading}>
			<DialogContent style={{display:"flex",justify:"center",flexDirection:"column"}}>
				<CircularProgress />
				<h4>{message}</h4>
			</DialogContent>
		</Dialog>
	);
	return <div>{dialog}</div>;
}

Loading.propTypes = {
	loading: PropTypes.bool.isRequired,
	message:PropTypes.string
}
Loading.defaultProps = {
	loading:false,
	message:"Cargando..."
}

export default Loading;
