import React, { useState,useEffect } from "react";
import PropTypes from "prop-types";
import { Grid, TextField, Button, Paper, Typography } from "@material-ui/core";
import { useDispatch, connect,useSelector } from "react-redux";
import styles from "./styles";
import SnackBarBase from "./../../litteComponents/SnackBar/index";
import { createTask } from "./actionsCreator";
import { withRouter } from "react-router";

function TaskCreate(props) {
  const { token, idUser, history, createdTask } = props;
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [message, setMessage] = useState("");
  const [type,setType]=useState("success");
  const dispatch = useDispatch();
  //setTimeout
  let s=null
  //const [disableBtn, setDisableBtn] = useState(true);
  //const dispatch = useDispatch();
  //const selector=useSelector(state=>state.createTask);
  //console.log(props);
  const classes = styles();
  useEffect(() => {
    //console.log("lol")
    //console.log(createdTask)
    if(createdTask.error){
      setType("error")
      setOpen(true);
      setMessage(createdTask.message)
    }
    if(createdTask.error==false){
      setType("success")
      setOpen(true);
      setMessage(createdTask.message)
      s=setTimeout(async () => {
        await handleClose();
        await dispatch({"type":"CLEAR_MESSAGE_CREATE"})
        history.push("/");
      }, 1500);
    }
    return(()=>{
      clearTimeout(s)
    })

  },[props.createdTask])

  const initTask = async (e) => {
    e.preventDefault();
    if (title.length > 5 && description.length > 5) {
     
    }
 
    await props.createTask(title, description, token, idUser)
    
     //await console.log(createdTask)
     /*if(createdTask.error) {
        console.log(createdTask)
     }else{
        console.log(createdTask)
     }*/

    /*console.log("Hola "+props.createdTask.error)
    if (props.createdTask.error) {
      setOpen(true);
      cleanState();
      await setMessage(props.createdTask.message)
      setTimeout(async () => {
        await handleClose();
        history.push("/");
      }, 2000);
    }*/
  };

  const cleanState = () => {
    setTitle("");
    setDescription("");
  };

  const handleClose = () => {
    setOpen(false);
    //console.log("cerrando");
  };

  //console.log(props);
  /*if (props.createdTask.error === false) {
    setOpen(true);

    console.log('entra');
  }*/
  return (
    <>
      <Grid container direction="column">
        <Paper className={classes.paper}>
          <form onSubmit={initTask}>
            <Typography style={{ textAlign: "center" }} variant="h5">
              Crear Tarea
            </Typography>
            <Grid item container direction="column">
              <TextField
                required
                label="Titlo"
                placeholder="Titlo"
                style={{ margin: 20 }}
                onChange={(e) => setTitle(e.target.value)}
                value={title}
              />
            </Grid>
            <Grid item container direction="column">
              <TextField
                required
                multiline
                rows={5}
                variant="outlined"
                label="Descripcin"
                placeholder="Descripcin"
                style={{ margin: 20 }}
                value={description}
                onChange={(e) => setDescription(e.target.value)}
              />
            </Grid>
            <Grid item container direction="column"></Grid>
            <Grid item container direction="column">
              <Button
                type="submit"
                color="primary"
                variant="contained"
                style={{ margin: 20 }}
              >
                Crear
              </Button>
            </Grid>
          </form>
        </Paper>
      </Grid>
      <SnackBarBase
        setOpen={setOpen}
        open={open}
        type={type}
        message={message}
        onClose={handleClose}
      />
    </>
  );
}

TaskCreate.propTypes = {
  token: PropTypes.string.isRequired,
  idUser: PropTypes.number.isRequired,
  history: PropTypes.object.isRequired,
  createdTask: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {
    createdTask: state.createTask,
  };
};
export default connect(mapStateToProps,{createTask})(withRouter(TaskCreate));
