const initialState = {
  error: null,
  status: null,
  message: null,
};
export const createTask = (state = initialState, action) => {
  switch (action.type) {
    case Actions.CREATE_TASK_SUCCESS:
      return {
        ...state,
        status: action.status,
        error: false,
        message: action.message,
      };
      break;
    case Actions.CREATE_TASK_ERROR:
      return {
        ...state,
        status: action.status,
        error: true,
        message: action.message,
      };
      break;
    case Actions.CLEAR_MESSAGE_CREATE:
      return { ...state, error: null, status: null, message: null };
    default:
      return state;
      break;
  }
};

