import Actions from "./../../redux/actionTypes.js";
import axios from "axios";

const url=`http://wanervalencia2019.pythonanywhere.com`

export const update_task = (
	idTask,
	idUser,
	token,
	title,
	description,
	completed,
	favorite
) => async (dispatch) => {
	const params = {
		title: title,
		Description: description,
		created_by: idUser,
		completed: completed,
		favorite: favorite,
	};
	const config = {
		headers: {
			Authorization: `Token ${token}`,
		},
	};
	console.log(params);
	axios
		.put(`${url}/api/v1/task/update/${idTask}/`, params,config)
		.then((res) => {
			console.log(res.status);
			return dispatch({
				type: Actions.UPDATE_SUCESS,
				data: res.data,
				message: "Tarea actualizada correctamente"
			});
		})
		.catch((err) => {
			const message=err.response ? err.response.data.message[0]: "Por favor verifica tu conexin"
			return dispatch({
				type: Actions.UPDATE_ERROR,
				message:"hola"
			});
		});
};
