import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { getTaskUncomplete, deleteTask } from "./actionCreators";
import { connect, useDispatch } from "react-redux";
import TaskView from "./../../components/TaskView";
import Loading from "./../../litteComponents/Loading/index.js";
import {
  Grid,
  Paper,
  Container,
  Typography,
  Fab,
  Tooltip,
} from "@material-ui/core";
import Pagination from "@material-ui/lab/Pagination";
import AddIcon from "@material-ui/icons/Add";
import TabsNav from "../../components/Tabs";
import { withRouter } from "react-router";
import Modal from "./../../litteComponents/Modal/index.js";
const Tasks = (props) => {
  const { getTaskUncomplete, idUser, token, history, update,loading } = props;

  const { tasks,total_pages } = props.tasks;

  const [page,setPage]=useState(1);
  //console.log(loading);
  const dispatch = useDispatch();
  //console.log(props.match.url == '/');
  useEffect(() => {
    async function getData() {
      await getTaskUncomplete(token,page);
    }
    getData();
  }, [update,page]);
  const deletet = (id, token) => {
    return dispatch(deleteTask(id, token));
  };
  if (loading.open) {
    return <Loading loading={loading.open} />;
  }
  const show = tasks.filter((t) => t.completed != true);

  return (
    <Container style={{ margin: 10 }}>
      <h4>Listando Tareas</h4>
      <Grid container direction="row" justify="center" spacing={2}>
        {show.length > 0 ? (
          show.map((task, i) => (
            <Grid key={i} item sm={6} md={4} lg={3} xs={12}>
              <TaskView
                key={i}
                title={task.title}
                description={task.Description}
                date={task.date}
                completed={task.completed}
                favorite={task.favorite}
                id={task.id}
                idUser={idUser}
                token={token}
                remove={() => deletet(task.id, token)}
                history={history}
              />
            </Grid>
          ))
        ) : (
          <div>
            <Typography variant="h4">No hay tareas pendientes</Typography>
          </div>
        )}
        <Grid container style={{ marginTop: "1%",marginBottom:"10%" }} justify="flex-end">
          <Tooltip
            placement="top"
            onClick={() => history.push("/nuevaTarea")}
            title="Crear tarea"
            aria-label="add"
          >
            <Fab color="secondary" aria-label="add">
              <AddIcon />
            </Fab>
          </Tooltip>
        </Grid>
      </Grid>
      <footer>
      <Grid style={{ backgroundColor: "#fff",width:"50%",borderRadius:30,marginRight:"auto",marginLeft:"auto"}} container  justify="center">
        {show.length > 0 ? (
          <Pagination
            shape="rounded"
            count={total_pages}
            page={page}
            color="primary"
            onChange={(e,current_page)=>setPage(current_page)}
          />
        ) : null}
      </Grid>
      </footer>
    </Container>
  );
};

Tasks.propTypes = {
  getTaskUncomplete: PropTypes.func.isRequired,
  idUser: PropTypes.number.isRequired,
  token: PropTypes.string.isRequired,
  tasks: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => {
  return {
    tasks: state.tasks,
    update: state.update,
    loading:state.loading
  };
};
const mapDispatchToProps = { getTaskUncomplete };

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Tasks));
