import { combineReducers } from 'redux';
import Login from '../screens/Login/reducer';
import { TasksReducer } from '../screens/Tasks/reducer';
import { createTask } from './../screens/TaskCreate/reducers';
import { updateTask } from './../screens/TaskUpdate/reducer.js';
import {account} from "./../screens/Configuration/reducer.js"
import {loading,snackBar} from "./../ReduxComponents/reducer.js"
export default combineReducers({
	login: Login,
	tasks: TasksReducer,
	createTask,
	update: updateTask,
	account,
	//REDUCERS DE LOS COMPONENTES
	loading,
	snackBar
	
});

