import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Grid, TextField, Button, Paper, Typography } from '@material-ui/core';
import { useDispatch, useSelector,connect } from 'react-redux';
import styles from './styles';
import SnackBarBase from './../../litteComponents/SnackBar/index';
import { update_task } from './actionCreator.js';
import { withRouter } from 'react-router';
//import { NavLink } from 'react-router-dom';

function TaskUpdate(props) {
  //props
  const { token, idUser, match, history,updateState } = props;
  const { id } = match.params;
  //console.log(match);
  //variables
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [message, setMessage] = useState("");
  const [type,setType]=useState("success");
  const task = useSelector((state) => state.tasks).tasks.filter(
    (t) => t.id == id
  )[0];
  const dispatch = useDispatch();

  let s=null
  //inicializador
  useEffect(() => {
    if (task) {
      setTitle(task.title);
      setDescription(task.Description);
    }
    if(updateState.error){
      setType("error")
      setOpen(true);
      setMessage(updateState.message)
    }
    if(updateState.error==false){
      setType("success")
      setOpen(true);
      setMessage(updateState.message)
       s=setTimeout(async () => {
        await handleClose();
        await dispatch({"type":"CLEAR_MESSAGE_UPDATE"})
        history.push("/");
      }, 1000);
    }
   return(()=>{
      //console.log(s)
    })

  }, [updateState]);


  //console.log(props);
  //FUNCIONES
  const classes = styles();

  const handleClose = () => {
    setOpen(false);
  };
  const update = async () => {
    await dispatch(update_task(id, idUser, token, title, description));
    //history.push('/');
  };
  //console.log(props);
  return (
    <Grid container  direction="column">
      <Paper className={classes.paper}>
        <Typography style={{ textAlign: 'center',marginTop:20}} variant="h5">
          Actualizar Tarea
        </Typography>
        <Grid item container direction="column">
          <TextField
            required
            label="Titlo"
            placeholder="Titlo"
            style={{ margin: 20 }}
            onChange={(e) => setTitle(e.target.value)}
            value={title ? title : ''}
          />
        </Grid>
        <Grid item container direction="column">
          <TextField
            required
            multiline
            rows={5}
            variant="outlined"
            label="Descripcin"
            placeholder="Descripcin"
            style={{ margin: 20 }}
            value={description ? description : ''}
            onChange={(e) => setDescription(e.target.value)}
          />
        </Grid>
        <Grid item container direction="column"></Grid>
        <Grid item container direction="column">
          <Button
            color="primary"
            variant="contained"
            onClick={update}
            style={{ margin: 20 }}
          >
            Actualizar
          </Button>
        </Grid>
      </Paper>
      <SnackBarBase
        open={open}
        setOpen={setOpen}
        message={message}
        type={type}
        onClose={() => handleClose}
      />
    </Grid>
  );
}


TaskUpdate.propTypes = {
  token: PropTypes.string.isRequired,
  idUser:PropTypes.number.isRequired,
  match:PropTypes.object.isRequired,
  //id proveniente del match.params.id
  //id:PropTypes.number.isRequired,
  //histoy de  react-router
  history:PropTypes.object.isRequired,
};
const mapStateToProps = (state) => {
  return {
    updateState: state.update,
  };
};

export default connect(mapStateToProps)(withRouter(TaskUpdate));
