import Actions from "./../../redux/actionTypes";
import axios from "axios";

const url=`http://wanervalencia2019.pythonanywhere.com`

export const createTask = (title, description, token, id) => async (
  dispatch
) => {
  const params = {
    title: title,
    Description: description,
    created_by: id,
  };
  const config = {
    headers: {
      Authorization: `Token ${token}`,
    },
  };
  //console.log(params)
  axios
    .post(`${url}/api/v1/task/create/`,params,config)
    .then((res) => {
      //console.log(res);
      dispatch({
        type:Actions.CREATE_TASK_SUCCESS,
        status: res.status,
        message:"Tarea creada con xito"
      });
    })
    .catch((err) => {
      //console.log(err.response.status)
      const message=err.response ? err.response.data.message[0]:"Por favor verifica tu conexin'a internet"
      dispatch({
        type: Actions.CREATE_TASK_ERROR,
        message:message,
        status:err.response ? err.response.status:500
      });
    });
};
export const snackOpen = () => {
  return {
    type: Actions.OPEN_SNACKBAR,
  };
};
