import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect, useDispatch} from "react-redux";
import {
  deleteTask,
  getTask,
  getTaskComplete,
} from "./../Tasks/actionCreators.js";
import TaskView from "./../../components/TaskView";
import Loading from "./../../litteComponents/Loading/index.js";
import { Grid,  Container, Typography,Fab,Tooltip } from "@material-ui/core";
import Pagination from "@material-ui/lab/Pagination";
import AddIcon from "@material-ui/icons/Add"
//import TabsNav from "../../components/Tabs";
import { withRouter } from "react-router";

const TaskCompletes = (props) => {
  const { getTaskComplete, idUser, token, history, update,loading } = props;
  const { tasks } = props.tasks;
  const dispatch = useDispatch();
  const [page,setPage]= useState(1)

  //console.log(props.match.url == '/');
  //console.log(tasks);
  useEffect(() => {
    async function getData() {
      await getTaskComplete(token,page);
    }
    getData();
  }, [update,page]);

  const deletet = (id, token) => {
    return dispatch(deleteTask(id, token));
  };
  const show = tasks.filter((t) => t.completed == true);
    if (loading.open) {
    return <Loading loading={loading.open} />;
  }
  return (
    <Container style={{ margin: 10 }}>
      <h4>Listando Tareas</h4>
      <Grid
        container
        direction="row"
        direction="row"
        justify="center"
        spacing={2}
      >
        {show.length > 0 ? (
          show.map((task, i) => (
            <Grid key={i} item sm={6} md={4} lg={3} xs={12}>
              <TaskView
                key={i}
                title={task.title}
                description={task.Description}
                date={task.date}
                completed={task.completed}
                favorite={task.favorite}
                id={task.id}
                idUser={idUser}
                token={token}
                remove={() => deletet(task.id, token)}
                history={history}
              />
            </Grid>
          ))
        ) : (
          <div>
            <Typography variant="h4">No hay tareas completas</Typography>
          </div>
        )}
      </Grid>
      <Grid container style={{ marginTop: 60 }} justify="flex-end">
        <Tooltip
          placement="top"
          onClick={() => history.push("/nuevaTarea")}
          title="Crear tarea"
          aria-label="add"
        >
          <Fab color="secondary" aria-label="add">
            <AddIcon />
          </Fab>
        </Tooltip>
      </Grid>
       <footer>
      <Grid style={{ backgroundColor: "#fff",width:"25em",borderRadius:30,marginRight:"auto",marginLeft:"auto"}} container  justify="center">
        {show.length > 0 ? (
          <Pagination
            shape="rounded"
            count={40}
            color="primary"
            onChange={(e,current_page)=>setPage(current_page)}
          />
        ) : null}
      </Grid>
      </footer>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    tasks: state.tasks,
    update: state.update,
    loading: state.loading
  };
};
const mapDispatchToProps = { getTaskComplete };

TaskCompletes.propTypes = {
  getTaskComplete: PropTypes.func.isRequired,
  idUser: PropTypes.number.isRequired,
  token: PropTypes.string.isRequired,
  tasks: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(TaskCompletes));
