import ActionsComponents from "./actionTypes.js";

const initialState = {
  open: false,
};

export const snackBar = (state = { open: false }, action) => {
  switch (action.type) {
    case ActionsComponents.OPEN_SNACKBAR:
      return { ...state, open: true };
      break;
    case ActionsComponents.CLOSE_SNACKBAR:
      return { ...state, open: false };
      break;
    default:
      return state;
      break;
  }
};
export const loading = (state = initialState, action) => {
  switch (action.type) {
    case ActionsComponents.OPEN_LOADING:
      return { ...state, open: true };
    case ActionsComponents.CLOSE_LOADING:
      return { ...state, open: false };
    default:
      return state;
  }
};
