import Actions from "./../../redux/actionTypes.js";

const initialState = {
	data: null,
	error: null,
	message: null,
};

export const updateTask = (state = initialState, action) => {
	switch (action.type) {
		case Actions.UPDATE_SUCESS:
			return {
				...state,
				data: action.data,
				message: action.message,
				error: false,
			};
			break;
		case Actions.UPDATE_ERROR:
			return { ...state, error: true, message: action.message };
			break;
		case Actions.CLEAR_MESSAGE_UPDATE:
			return { ...state, data: null, error: null, message: null };
		default:
			return state;
			break;
	}
};
